import React, { Component } from 'react';
import Axios from 'axios';
import Toast from '../../components/toast';
import './layout.css';
import API from '../../utils/Api';
class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            countdown: 60,
            verifyTxt: "",
            canSendCode: true,
            mobile: '',
            verifyCode: '',
            name: '',
            channel: null,
            img1: require('./imgs/bg.png'),
            img2: require('./imgs/btn-long.png'),
            img3: require('./imgs/btn-short.png')
        }
    }

    componentWillMount() {
        const sources = this.GetLocaltionParams('source');
        console.log(sources, '');
        if (sources) {
            this.setState({
                channel: sources
            })
        }
    }

    GetLocaltionParams(name) {
        const channel = window.location.search.replace('?', '');
        let result = null;
        if (channel.length) {
            const channelArr = channel.split('&');
            channelArr.some(item => {
                const _item = item.split("=");
                if (_item[0] === name) {
                    result = _item[1]
                }
            })
        }

        return result;
    }


    ValidMobile(val) {
        return (/^1[3456789]\d{9}$/.test(val));
    }

    countdown() {
        let { countdown } = this.state;
        if (this.state.countdown === 1) {
            this.setState({
                verifyTxt: "",
                canSendCode: true,
                countdown: 60,
            });
            return false;
        }
        else {
            this.setState({
                countdown: --countdown,
                canSendCode: false,
                verifyTxt: `${countdown}`
            })
        }
        if (this.TimeOutCount != null) clearTimeout(this.TimeOutCount);
        this.TimeOutCount = setTimeout(() => {
            this.countdown();
        }, 1000);
    }

    GetVerifyCode() {
        const { mobile } = this.state;
        if (!mobile) {
            Toast.show('');
            return false;
        }
        if (!this.ValidMobile(mobile)) {
            Toast.show('');
            return false;
        }


        const req = Axios.get(`${API.send_msg}/${mobile}`, {
            // const req = Axios.get('https://www.easy-mock.com/mock/5bd913e61baba44579100308/api/send', {
            params: {
                cell: mobile
            }
        });

        req.then((res) => {
            const data = res.data;
            if (data.success) {
                Toast.show('');
                this.countdown();
            } else {
                Toast.show('');
            }
        }).catch(err => {
            Toast.show('');
        });

    }


    submit() {
        const { name, mobile, verifyCode, channel } = this.state;
        if (!name) {
            Toast.show('');
            return false;
        }
        if (!mobile) {
            Toast.show('');
            return false;
        }
        if (!this.ValidMobile(mobile)) {
            Toast.show('');
            return false;
        }

        if (!verifyCode && verifyCode.length !== 4) {
            Toast.show('');
            return false;
        }


        /* Ajax  */
        const req = Axios.post(API.sign_insert, {
            "clientCell": mobile,
            "clientName": name,
            "code": verifyCode,
            "source":channel
        });


        req.then((res) => {
            const data = res.data;
            if (data.success) {
                Toast.show('');
            } else {
                Toast.show(data.message);
            }
        }).catch(err => {
            Toast.show('');
        });

    }

    render() {
        const { mobile, name, verifyCode, verifyTxt, canSendCode } = this.state;
        return (
            <div className="main">
                <div className='form'>
                    <div className='f-item'>
                        <label className='f-label'></label>
                        <div className='f-input'>
                            <input
                                type='text'
                                value={name}
                                onChange={(event) => {
                                    this.setState({ name: event.target.value })
                                }}
                                placeholder='' className='input' />
                        </div>
                    </div>
                    <div className='f-item'>
                        <label className='f-label'></label>
                        <div className='f-input'>
                            <input
                                type='number'
                                pattern="[0-9]*"
                                onChange={(event) => {
                                    this.setState({ mobile: event.target.value })
                                }}
                                value={mobile} placeholder='' className='input' />
                        </div>
                    </div>
                    <div className='f-item f-item-verify'>
                        <label className='f-label'></label>
                        <div className='f-input f-input-verify'>
                            <input
                                type='number'
                                pattern="[0-9]*"
                                onChange={(event) => {
                                    this.setState({ verifyCode: event.target.value })
                                }}
                                value={verifyCode} placeholder='' className='input' />
                        </div>
                        <div
                            onClick={() => {

                                canSendCode && this.GetVerifyCode();
                            }}
                            className='f-btn-verify'>{verifyTxt}</div>
                    </div>
                    <div onClick={() => {
                        this.submit();
                    }} className='f-btn'></div>
                </div>
                {/*  */}
                <div className='intro'>
                    <p>0.65+3</p>
                    <p>1(http://t.cn/RXEVOLk)</p>
                    <p>2Mcc</p>
                    <p>3</p>
                    <p>495516</p>
                </div>
            </div>
        );
    }
}

export default App;



